.pro{
    overflow: visible;
    margin-top: 10vh;
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content: center;
    align-items: center;
}
.pro1{
    width:1380px;
}
.prohead{
    padding-top:5vh;
    font-size: 32px;
    color: #61dafb;
}
.wholepro{
    text-align: center;
}
.cards{
    margin-top: 80px;
    display: grid;
    grid-template-columns: repeat(3,auto);
    justify-content: space-evenly;
    align-items: center;
}
.card{
    height: 250px;
    width: 100%;
}
.codes{
    display: grid;
    grid-template-columns: repeat(2,120px);
    justify-content:flex-end;
    align-items: center;
}
.links{
    color: #61dafb;
    width: 70px;
}
.car{
    text-align: center;
    color:#61dafb;
    border-style:groove;
    border-width:1px;
    border-color:grey;
    width:274px;
    height:350px;
    border-radius: 10px;
    cursor: pointer;
    z-index: 10;
    transition: all ease-in-out 0.3s ;
}
.car:hover{
    border-color: #61dafb;
    transform: scale(1.03);
    box-shadow: 0px 0px 0px 1px  #61dafb;
}

@media (max-width:1126px) {
    .cards{
        display: grid;
        grid-template-columns:repeat(1,auto);
        justify-content: center;
        gap: 10vh;
        align-items: center;
    }
    .car{
        width: 70%;
        margin-left: 15%;
        height: 350px;
    }
    .prohead{
        font-size: 24px;
    }
    .keep{
        font-size: 26px;
    }
}