
.nav{
    display: flex;
    justify-content:flex-end;
    overflow: hidden;
    position:fixed;
    margin-top: 2vh;
    width: 100%;
    z-index: 5;
}

.elements{
    display: flex;
    flex-direction: row;
    list-style-type: none;
}
a:link{
    text-decoration: none;
    margin-right: 6vh;
    padding: 3px;
    
}
.comp:hover{
    color: #61dafb;
}
.comp {
    color: grey;
    width: 30%;
}
.toggicon{
    display: none;
}


/* Responsive media */

@media (max-width:699px) {
    .elements{
        display: none;
    }
    .nav{
        z-index: 6;
    }
    .toggicon{
        display: block;
        background-color:wheat ;
        position:absolute;
        margin-top: 5cm;
        z-index: 7;
    }
    
}

@media (max-width:1050px) {
    .elements{
        margin-right: -3vh;
    }
}


