.conwhole{
     margin-top: 10vh;
    text-align: center;
    justify-content: center;
    padding-bottom: 23vh;
    align-items: center;
    z-index: -20;
}
.head{
    padding-top: 10vh;
    font-size: 23px;
    z-index: -11;
}

.feel{
    color: grey;
    position: relative;
    margin-top: -50px;
    z-index: -12;

}
.a{
    color: #61dafb;
    margin-left: 8vh;
}
.condet{
    margin-top: 80px;
    z-index: 11;
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content: center;
    align-items: center;
    height:200px;
    width: 100%;
}
.mail,.phone{
    height: 40px;
    z-index: 1;
    border-style: groove;
    border-width: 1px;
    border-color:#61dafb;
    background-color: #282c34;
    align-items: center;
    justify-content: center;
    padding-top: 1.5vh;
    border-radius: 25px;
}
.icon{
    position: absolute;
    margin-right: -2vw;
    margin-left: 4vh;
    font-size: 18px;
    margin-top: 4px;
}
.rights{
    padding-bottom: 2vw;
    display: flex;
    justify-content: center;
    text-align: center;
    color: grey;
}

@media (max-width:1100px) {
    .conwhole{
        margin-top: 40vh;
        margin-left: 2px;
        padding-bottom: 30vh;
    }
    .head{
        margin-top: -40vh;
        font-size: 19px;
    }
    .con{
        margin-left: -2vh;
    }
}


