.info{
    display: grid;
    grid-template-columns: repeat(2,40%);
    justify-content: center;
    margin-top: 22vh;  
    height: 70%; 
} 
.whl{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content: center;
    align-items: center;
}
.whl1{
    width:1380px;
}
.logo{
    margin-top: 2.3vh;
    position: absolute;
    margin-left: 4vw;
    font-size: 24px;
    color: grey;
    cursor: pointer;
}


/* details */

.details{
    z-index: 5;
    height: 450px;
}
.inner{
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content:flex-start;
    margin-top: 90px;
    margin-left: 90px;
}
.social{
    display: grid;
    grid-template-columns: repeat(2,70px);
    height:100%;
    width: 100%;
    justify-content:flex-start;
    align-items: center;
}
.socialbut{
    border-width: 1px;
    position: relative;
    border-style: groove;
    border-radius: 20%;
    color: #61dafb;
    border-color: #61dafb;
    background-color:#282c34 ;
    font-size: 1.3em;
    overflow: hidden;
}
.socialbut:hover
{
   color: #282c34;
   background-color: #61dafb;
   border-color: #61dafb;
}

/* profile */

.profile{
    z-index: -3;
    display: flex;
    margin-left: -3vw;
}
.img{
    position: absolute;
    width: 240px;
    margin-top: 100px;
    margin-left: 3%;
    border-radius: 50%;
    border-style: groove;
    border-width: 3px;
    border-color: #61dafb;
    animation: animateimg 1s;
    animation-iteration-count: infinite;
}
@keyframes animateimg{
    0%, 20%, 40%, 80%, 100%{tranform:translateY(0);}
    50% {transform: translateY(-5px);}
    60%{transform: translateY(-5px);}
}
.tech{
    width:100%;
    height:100%;
    z-index: -1;
    display: grid;
    justify-content: flex-end;
    margin-left: -30px;
}
.node,.react,.mongo{
    border-style: groove;
    border-width: 1px;
    border-radius: 50%;
    border-color: #61dafb;
    display: grid;
    justify-content: center;
    align-items: center;
}
@keyframes nodeani {
    from{
        margin-left: -380px;
        margin-top: 80px;
    }
    to{
        margin-left: -65px;
    }
}
@keyframes reactani {
    from{
        margin-left:-280px ;
        margin-top: -30px;
    }
    to{
        margin-left: 25px;
    }
}
@keyframes mongoani {
    from{
        margin-top: -180px;
        margin-left: -480px;
    }
    to{
        margin-top: 0vh;
        margin-left: -110px;
    }
}
.node{
    animation: nodeani 0.75s;
    margin-left: -65px;
    font-size: 68px;
    height: 98px;
    width: 98px;
}
.react{
    animation: reactani 0.85s;
    font-size: 98px;
    margin-left: 25px;
    height: 118px;
    width: 118px;
}
.mongo{
    animation: mongoani 0.95s;
    font-size: 118px;
    margin-left: -110px;
    height: 148px;
    width: 148px;
}

.det{
    animation: trans2 0.55s;
}
@keyframes trans2 {
    from{
        margin-left: -100%;
    }
    to{
        margin-left: 0%;
    }
}
.soc{
    animation: soc 0.75s;
    height: 50px;
    
}
@keyframes soc {
    from{margin-left: -100vh;}
    to{margin-left:0vh;}
}

/* media responsive */

@media (max-width:1050px) {
    html{
        overflow-x: hidden;
    }
    .info{
        margin-top: 15vh;
        display: grid;
        grid-template-columns: repeat(1,auto);
        justify-content: center;
    }
    .inner{
        margin-left: 0px;
    }
    .soc{
        display: grid;
        grid-template-columns: repeat(2,auto);
        justify-content: center;
        align-items: center;
    }
    .social{
        margin-left: 30px;
    }
    .details
    {
        text-align: center;
        display: grid;
        grid-template-columns: repeat(1,auto);
        justify-content: center;
        align-items: center;
       order: 2;
    }
    .profile{
        display: grid;
        grid-template-columns: repeat(1,auto);
        justify-content: center;
        height: 500px;
        width: 600px;
    }
    .img{
        margin-left: -2vw;
    }
    .tech{
        rotate: 90deg;
        height: 400px;
        margin-top: 195px;
        margin-left:1px;
        width: 200px;
    }
    .node,.react,.mongo{
        rotate: -90deg;
        font-size: 70px;
    }
    .node{
        margin-left: -30px;
        margin-top: 20px;
        height: 80px;
        width: 80px;
        animation: none;
    }
    .react{
        height: 100px;
        width: 100px;
        animation: none;
    }
    .mongo{
        margin-left: -50px;
        height: 130px;
        width: 130px;
        animation: none;
    }
}

@media (max-width:400px) {
    .node{
        margin-left: -20px;
        margin-top: 30px;
    }
    .react{
       margin-top: -20px;
    }
    .mongo{
        margin-left: -50px;
        margin-top: -20px;
    } 
}

