.whole{
    overflow: visible;
    margin-top: 10vh;
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content: center;
    align-items: center;
}
.abt1{
    text-align: center;
    width:1380px;
}
.abthead{
    padding-top: 5vh;
    color: #61dafb;
    font-size: 32px;
}
.keep{
    color:rgb(177, 175, 175);
    font-size: 38px;
}
.about{
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(2,auto);
    justify-content:center;
    align-items: center;
}
.aboutimg{
    
    margin-left: -70px;
    padding-right:70px;
    height: 100%;
    width: 500px;
}
.application{
    font-size: 60px;
}
.logos1,.logos2,.logos3,.logos4,.logos5{
    border-radius: 50%;
    border-style: groove;
    border-width: 2px;
    border-color: #61dafb;
    
}
.logos1{
    padding-top: 5px;
    margin-left: 250px;
    width: 85px;
    height: 75px;
    animation: 0.5s;
}
.logos2{
    margin-top: 40px;
    padding-top: 5px;
    margin-left: 360px;
    width: 95px;
    height: 80px;
}
.logos3{
    margin-left: 150px;
    width: 100px;
    height: 90px;
    padding-top: 10px;
    margin-top: -50px;
}
.logos4{
    padding-top: 5px;
    margin-left: 320px;
    width: 90px;
    height: 80px;
}
.logos5{
    margin-left: 200px;
    width: 70px;
    height: 70px;
}



.para{
    margin-left: -30px;
    border-style: groove;
    border-left: 3px solid;
    border-right: none;
    border-top: none;
    border-bottom: none;
    border-color: #61dafb;
    height: 60%;
    width: 500px;

    color: grey;
    align-items: center;
    justify-content: center;
}
.para1{
    margin-top: 18%;
}

@media (max-width:1000px) {
    .abthead{
        font-size: 24px;
    }
    .about{
        display: grid;
        grid-template-columns: repeat(1,auto);
        justify-content: center;
        align-items: center;
        margin-left: 20px;
    }
    .para{
        margin-left: 2vw;
        width: 100%;
        display: grid;
        justify-content: center;
        border: none;
    }
    .para1{
        padding-left: 5px;
        margin-left: -20px;
        border-left: 3px solid #61dafb;
    }
}