@media (min-width:1000px) {

.sidenavele{
    display: grid;
    grid-template-columns: repeat(1,auto);
    width: 2vh;
    height: 100%;
    justify-content: center;
    align-items: center;
    gap: 1vh;
}
.bor1{
    float:right;
    font-size: 10px;
}
.sidenav{
    position: absolute;
    height: 100%;
    z-index: 0;
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

.ele{
    height: 12vh;
    margin-top: 45vh;
}
ul li{
    list-style-type:none;
}

}

@media (max-width:1150px) {
    .sidenav{
        display: none;
    }
}
@media (min-height:1010px) {
     .sidenav{
        display: none;
     }
}